.signin-outer {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-image: url("../../../assets/img/background.jpg");
}

.lang {
  position: absolute;
  top: 12px;
  right: 16px;
}

.card-signin {
  border-radius: 12px;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 390px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  background-color: #fff;
  box-shadow: 3px 4px 16px 0px #ffffff33;
}

.btn {
  border-radius: 30px !important;
  height: 40px !important;
  width: 85%;
  margin-top: 23px;
  background-color: #fd6400;
  box-shadow: none;
  font-weight: bold;
}

.card-body {
  margin: 0 auto;
  width: 80%;
}

.w-75 {
  width: 30% !important;
}

.info {
  color: #3e3e3e;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 12px;
  font-weight: bold;
}

.icon {
  position: relative;
  top: -7px;
  width: 11px;
}

.signin-outer .mat-form-field-wrapper {
  padding-bottom: 4px;
}

@media (max-width: 1025px) {
  .card-signin {
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }
}

@media only screen and (max-width: 550px) {
  .card-signin {
    border-radius: 40px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
