/* HTML: <div class="loader"></div> */
.gigya-loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: radial-gradient(farthest-side, #f65c00 94%, #0000) top/8px 8px
      no-repeat,
    conic-gradient(#0000 30%, #f65c00);
  -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 8px), #000 0);
  animation: l13 1s infinite linear;
}

@keyframes l13 {
  100% {
    transform: rotate(1turn);
  }
}

.loadercont {
  width: 30%;
  height: 20vh;
  background: none;
  position: absolute;
  right: 10%;
  top: 35%;
  display: flex;
  justify-content: center;
  align-items: center;
}

